@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body{
	font-family: IBM Plex Sans, sans-serif;
}

.navbar {
  background: #fff;
}

@media (min-width: 1400px) {
  .nav_container {
    max-width: 90vw;
  }
}

.navbar-collapse {
  justify-content: space-between;
}

.collapse_component {
  margin: 0.5em 0;
}

.dropdown_wrapper {
  display: flex;
  justify-content: center;
}

.navbar-toggler,
.navbar-toggler:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

.search_bar_wrapper {
  flex: 1 1;
  display: flex;
  justify-content: center;
}

.search_bar {
  padding: 0.3em 1em;
  border-radius: 20px;
  background: #f2f2f2;
}

.search_bar .form-control,
.search_bar .form-control:focus {
  background: #f2f2f2;
  outline: none;
  border: none;
  box-shadow: none;
  font-family: IBM Plex Sans;
  font-weight: 500;
  font-size: 0.8em;
  width: 25em;
}

button.dropdown-toggle,
button.dropdown-toggle:focus {
  outline: none;
  border: none;
  box-shadow: none;
  font-weight: 500;
  font-size: 0.9em;
  color: #2e2e2e;
}

.dropdown_span {
  color: #2f6ce5;
}

.profile_image{
  width: 36px;
  height: auto;
  margin-right: 1em;
}

.header_card{
	border: none;
	margin-top: 4em;
}

.header_card .card-img{
	height: 30em;
	object-fit: cover;
}

.header_card_overlay{
	display: flex;
    align-items: flex-end;
	font-family: IBM Plex Sans;
}

.header_card_overlay .card-title{
	font-size: 2em;
	font-weight: 700;
}

.header_card_overlay .card-text{
	font-weight: 400;
}

.header_card_overlay .container{
	margin-bottom: 3em;
}



.sub_navigation{
    border-bottom: 2px solid #E0E0E0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    background: #fff;
}

.sticky{
    position: -webkit-sticky;
    position: sticky;
    top: 4.5em;
    z-index: 2;
}

.sub_navigation .nav-link{
    color: #8A8A8A;
    padding: 1em;
    border-bottom: 2px solid transparent;
}
.sub_navigation .nav-link.active{
    color: #000;
    border-bottom: 2px solid #000;
}

.sub_nav_buttons{
    display: flex;
    align-items: center;
}

.sub_nav_button button{
    height: 47px;
}

.sub_nav_button button,
.sub_nav_button button:focus{
    outline: none;
    border: none;
    box-shadow: none;
}

.sub_nav_button{
    width: auto;
    margin-left: 1em;
}

.sub_nav_button.dropdown button.dropdown-toggle{
    background: #EDEEF0;
    color: #000;
    box-shadow: none;
}

.sub_nav_button button.dropdown-toggle:focus{
    background: #BFBFC0;
    box-shadow: none;
}

.show>.btn-primary.dropdown-toggle:focus{
    box-shadow: none;
}

.sub_nav_button.join_group button{
    padding: 0.5em 1em;
    background: #2F6CE5;
    font-size: 1em;
}

.sub_nav_button.join_group button.leave{
    background: transparent;
    color: #6A6A6B;
    border: solid 0.8px #989899;
}
.body_section{
    display: flex;
}

.Sidebar{
    width: 30%;
    float: right;
    font-family: IBM Plex Sans;
}

.location_input{
    margin-top: 2em;
    border-bottom: 1px solid #B8B8B8;
}

.location_input .form-control{
    border: none;
    border-radius: 0;
}

.location_input .form-control:focus{
    box-shadow: none;
  }

.sidebar_alert{
    margin-top: 2em;
    display: flex;
    font-size: 0.9em;
    color: #808080;
}

.Sidebar svg{
    width: 24px;
    height: 24px;
}

.sidebar_alert svg,
.sidebar_recommend_head svg{
    margin-right: 1em;
}

.sidebar_recommend_head{
    margin-top: 2em;
    text-transform: uppercase;
    display: flex;
    align-items: center;
}

.sidebar_group{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
}

.sidebar_group button{
    outline: none;
    border: none;
    box-shadow: none;
    padding: 0.3em 1em;
    border-radius: 20px;
    background: #EDEEF0;  
}

.group_seemore{
    margin-top: 2em;
    text-align: right;
    color: #2F6CE5;
    font-size: 0.9em;
    font-weight: 400;
    cursor: pointer;
}

.sidebar_group p{
    flex: 1 1;
    margin: 0;
}
.post_card{
    margin-top: 1em;
    width: 45em;
    max-width: 100vw;
    font-family: IBM Plex Sans, sans-serif;
}

.post_card_img.card-img-top{
    height: 15em;
    object-fit: cover;
}

.post_card_category{
    color: #000;
    font-weight: 500;
}

.post_card_head{
    display: flex;
    justify-content: space-between;
}

.post_card_body_text{
    color: #5C5C5C;
    font-weight: 400;
}

.post_card .card-title{
    font-weight: 600;
}
.post_card_sub_detail{
    display: flex;
    font-weight: 500; 
}

.post_card_sub_detail>div{
    margin-right: 5em;
}
.post_card_dropdown .dropdown-toggle::after{
    content: none;
}

.post_card_sub_detail svg{
    margin-right: 0.5em;
}

.post_card_action{
    width: 100%;
    margin: 1em 0;
    font-weight: 600;
    border: solid 0.7px #A9AEB8;
}

.post_card_action_Meetup{
    color: #E56135;
}
.post_card_action_Meetup:hover,
.post_card_action_Meetup:active{
    background-color: #E56135;
    border-color: #E56135;
}
.post_card_action_Job{
    color:#02B875;
}
.post_card_action_Job:hover,
.post_card_action_Job:active{
    background-color: #02B875;
    border-color: #02B875;
}

.post-card-bottom{
    display: flex;
    justify-content: space-between;
    padding: 1em 0;
}

.post-card-bottom-left .card-text{
    font-weight: 600;
    margin-left: 1em;
}

.post-card-bottom-left,
.post-card-bottom-right{
    display: flex;
    align-items: center;
}

.post-card-views{
    color: #525252;
    font-weight: 500;
}

.post-card-views svg{
    margin-right: 0.5em;
}

.post-card-share{
    background: #EDEEF0;
    padding: 0.5em 1em;
    border-radius: 4px;
    margin-left: 2em;
}

.modal_body {
  display: flex;
  font-family: IBM Plex Sans;
}

.modal_alert {
  font-size: 0.8em;
  text-align: center;
}

.modal_body_div{
  padding: 0 1em 1em 1em;
}

.modal_body_left{
  width: 50%;
}

.modal_auth_title {
  font-weight: 700;
  margin-bottom: 1em;
}


.modal_body .form-control:focus{
  box-shadow: none;
}

.modal_body .form-control {
  border-radius: 0;
  font-size: 0.8em;
  font-weight: 500;
  padding: 1em;
  background: #F7F8FA;
}


.modal_right_text.text_top{
  font-size: 0.8em;
  text-align: right;
  font-weight: 400;
  color: #3D3D3D;
}
.modal_right_text.text_bottom{
  font-size: 0.7em;
}

.auth_switch {
  color: #2f6ce5;
  font-weight: 600;
  cursor: pointer;
}

.no_border_bottom {
  border-bottom: none;
}
.no_border_left {
  border-left: none;
}
.no_border_right {
  border-right: none;
}
.no_border_top {
  border-top: none;
}
.no_padding_right {
  padding-right: 0;
}
.no_padding_left {
  padding-left: 0;
}

.modal_button {
  margin-top: 1em;
  width: 100%;
  font-size: 0.8em;
  padding: 0.7em;
}

.modal_button.main{
  border-radius: 30px;
  font-weight: 600;
  background: #2F6CE5;
}

.modal_button.sec{
  border: 1px solid #D9D9DB;
}

.modal_wrapper .modal-content{
  position: relative;
  border-radius: 10px;
}

.modal_wrapper .modal-dialog{
  max-width: 900px;
}

.modal_cancel {
  position: absolute;
  top: -2em;
  right: -1em;
  cursor: pointer;
}
